// Breakpoint viewport sizes and media queries.
//
// Breakpoints are defined as a map of (name: minimum width), order from small to large:
//
//    (***REMOVED***s: 0, sm: 576p***REMOVED***, md: 768p***REMOVED***, lg: 992p***REMOVED***, ***REMOVED***l: 1200p***REMOVED***, ***REMOVED******REMOVED***l: 1400p***REMOVED***)
//
// The map defined in the `$grid-breakpoints` global variable is used as the `$breakpoints` argument by default.

// Name of the ne***REMOVED***t breakpoint, or null for the last breakpoint.
//
//    >> breakpoint-ne***REMOVED***t(sm)
//    md
//    >> breakpoint-ne***REMOVED***t(sm, (***REMOVED***s: 0, sm: 576p***REMOVED***, md: 768p***REMOVED***, lg: 992p***REMOVED***, ***REMOVED***l: 1200p***REMOVED***, ***REMOVED******REMOVED***l: 1400p***REMOVED***))
//    md
//    >> breakpoint-ne***REMOVED***t(sm, $breakpoint-names: (***REMOVED***s sm md lg ***REMOVED***l ***REMOVED******REMOVED***l))
//    md
@function breakpoint-ne***REMOVED***t($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {
  $n: inde***REMOVED***($breakpoint-names, $name);
  @if not $n {
    @error "breakpoint `#{$name}` not found in `#{$breakpoints}`";
  }
  @return if($n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);
}

// Minimum breakpoint width. Null for the smallest (first) breakpoint.
//
//    >> breakpoint-min(sm, (***REMOVED***s: 0, sm: 576p***REMOVED***, md: 768p***REMOVED***, lg: 992p***REMOVED***, ***REMOVED***l: 1200p***REMOVED***, ***REMOVED******REMOVED***l: 1400p***REMOVED***))
//    576p***REMOVED***
@function breakpoint-min($name, $breakpoints: $grid-breakpoints) {
  $min: map-get($breakpoints, $name);
  @return if($min != 0, $min, null);
}

// Ma***REMOVED***imum breakpoint width.
// The ma***REMOVED***imum value is reduced by 0.02p***REMOVED*** to work around the limitations of
// `min-` and `ma***REMOVED***-` prefi***REMOVED***es and viewports with fractional widths.
// See https://www.w3.org/TR/mediaqueries-4/#mq-min-ma***REMOVED***
// Uses 0.02p***REMOVED*** rather than 0.01p***REMOVED*** to work around a current rounding bug in Safari.
// See https://bugs.webkit.org/show_bug.cgi?id=178261
//
//    >> breakpoint-ma***REMOVED***(md, (***REMOVED***s: 0, sm: 576p***REMOVED***, md: 768p***REMOVED***, lg: 992p***REMOVED***, ***REMOVED***l: 1200p***REMOVED***, ***REMOVED******REMOVED***l: 1400p***REMOVED***))
//    767.98p***REMOVED***
@function breakpoint-ma***REMOVED***($name, $breakpoints: $grid-breakpoints) {
  $ma***REMOVED***: map-get($breakpoints, $name);
  @return if($ma***REMOVED*** and $ma***REMOVED*** > 0, $ma***REMOVED*** - .02, null);
}

// Returns a blank string if smallest breakpoint, otherwise returns the name with a dash in front.
// Useful for making responsive utilities.
//
//    >> breakpoint-infi***REMOVED***(***REMOVED***s, (***REMOVED***s: 0, sm: 576p***REMOVED***, md: 768p***REMOVED***, lg: 992p***REMOVED***, ***REMOVED***l: 1200p***REMOVED***, ***REMOVED******REMOVED***l: 1400p***REMOVED***))
//    ""  (Returns a blank string)
//    >> breakpoint-infi***REMOVED***(sm, (***REMOVED***s: 0, sm: 576p***REMOVED***, md: 768p***REMOVED***, lg: 992p***REMOVED***, ***REMOVED***l: 1200p***REMOVED***, ***REMOVED******REMOVED***l: 1400p***REMOVED***))
//    "-sm"
@function breakpoint-infi***REMOVED***($name, $breakpoints: $grid-breakpoints) {
  @return if(breakpoint-min($name, $breakpoints) == null, "", "-#{$name}");
}

// Media of at least the minimum breakpoint width. No query for the smallest breakpoint.
// Makes the @content apply to the given breakpoint and wider.
@mi***REMOVED***in media-breakpoint-up($name, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($name, $breakpoints);
  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media of at most the ma***REMOVED***imum breakpoint width. No query for the largest breakpoint.
// Makes the @content apply to the given breakpoint and narrower.
@mi***REMOVED***in media-breakpoint-down($name, $breakpoints: $grid-breakpoints) {
  $ma***REMOVED***: breakpoint-ma***REMOVED***($name, $breakpoints);
  @if $ma***REMOVED*** {
    @media (ma***REMOVED***-width: $ma***REMOVED***) {
      @content;
    }
  } @else {
    @content;
  }
}

// Media that spans multiple breakpoint widths.
// Makes the @content apply between the min and ma***REMOVED*** breakpoints
@mi***REMOVED***in media-breakpoint-between($lower, $upper, $breakpoints: $grid-breakpoints) {
  $min: breakpoint-min($lower, $breakpoints);
  $ma***REMOVED***: breakpoint-ma***REMOVED***($upper, $breakpoints);

  @if $min != null and $ma***REMOVED*** != null {
    @media (min-width: $min) and (ma***REMOVED***-width: $ma***REMOVED***) {
      @content;
    }
  } @else if $ma***REMOVED*** == null {
    @include media-breakpoint-up($lower, $breakpoints) {
      @content;
    }
  } @else if $min == null {
    @include media-breakpoint-down($upper, $breakpoints) {
      @content;
    }
  }
}

// Media between the breakpoint's minimum and ma***REMOVED***imum widths.
// No minimum for the smallest breakpoint, and no ma***REMOVED***imum for the largest one.
// Makes the @content apply only to the given breakpoint, not viewports any wider or narrower.
@mi***REMOVED***in media-breakpoint-only($name, $breakpoints: $grid-breakpoints) {
  $min:  breakpoint-min($name, $breakpoints);
  $ne***REMOVED***t: breakpoint-ne***REMOVED***t($name, $breakpoints);
  $ma***REMOVED***:  breakpoint-ma***REMOVED***($ne***REMOVED***t, $breakpoints);

  @if $min != null and $ma***REMOVED*** != null {
    @media (min-width: $min) and (ma***REMOVED***-width: $ma***REMOVED***) {
      @content;
    }
  } @else if $ma***REMOVED*** == null {
    @include media-breakpoint-up($name, $breakpoints) {
      @content;
    }
  } @else if $min == null {
    @include media-breakpoint-down($ne***REMOVED***t, $breakpoints) {
      @content;
    }
  }
}
